import React from "react";

type TableProps = {
  columns: { key: string; label: string }[];
  data: Record<string, React.ReactNode>[];
  selectable?: boolean;
};

export default function Table({ columns, data, selectable = false }: TableProps) {
  return (
    <div className="relative overflow-x-auto shadow-md sm:rounded-lg">
      <table className="w-full text-sm text-left text-text-light dark:text-text-dark bg-surface-light dark:bg-surface-dark border border-border-light dark:border-border-dark">
        <thead className="text-xs uppercase bg-background-light dark:bg-background-dark text-muted-light dark:text-muted-dark border-b border-border-light dark:border-border-dark">
          <tr>
            {selectable && (
              <th scope="col" className="p-4">
                <div className="flex items-center">
                  <input
                    id="checkbox-all"
                    type="checkbox"
                    className="w-4 h-4 text-primary bg-surface-light border-border-light rounded-sm focus:ring-primary dark:focus:ring-primary-dark dark:ring-offset-surface-dark dark:focus:ring-offset-surface-dark focus:ring-2 dark:bg-surface-dark dark:border-border-dark"
                  />
                  <label htmlFor="checkbox-all" className="sr-only">
                    checkbox
                  </label>
                </div>
              </th>
            )}
            {columns.map((col) => (
              <th key={col.key} scope="col" className="px-6 py-3 font-semibold">
                {col.label}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.map((row, i) => (
            <tr
              key={i}
              className="bg-surface-light dark:bg-surface-dark border-b border-border-light dark:border-border-dark hover:bg-background-light dark:hover:bg-background-dark transition-colors"
            >
              {selectable && (
                <td className="w-4 p-4">
                  <div className="flex items-center">
                    <input
                      id={`checkbox-table-${i}`}
                      type="checkbox"
                      className="w-4 h-4 text-primary bg-surface-light border-border-light rounded-sm focus:ring-primary dark:focus:ring-primary-dark dark:ring-offset-surface-dark dark:focus:ring-offset-surface-dark focus:ring-2 dark:bg-surface-dark dark:border-border-dark"
                    />
                    <label htmlFor={`checkbox-table-${i}`} className="sr-only">
                      checkbox
                    </label>
                  </div>
                </td>
              )}
              {columns.map((col, j) =>
                j === 0 ? (
                  <th
                    key={col.key}
                    scope="row"
                    className="px-6 py-4 font-medium text-text-light dark:text-text-dark whitespace-nowrap"
                  >
                    {row[col.key]}
                  </th>
                ) : (
                  <td key={col.key} className="px-6 py-4">
                    {row[col.key]}
                  </td>
                )
              )}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}